body {
	background-color: #48c9b0;
	padding: 20px;
	margin: 0;
}
h1,
h2,
p,
ul,
li {
	font-family: sans-serif;
}
ul.header li {
	display: inline;
	list-style-type: none;
	margin: 0;
}
ul.header {
	background-color: #111;
	padding: 0;
	border-radius: 20px;
}
ul.header li a {
	color: #fff;
	font-weight: bold;
	text-decoration: none;
	padding: 20px;
	display: inline-block;
}
.content {
	background-color: #f9f9f9;
	padding: 20px;
	border-radius: 20px;
}
.content h2 {
	padding: 0;
	margin: 0;
}
.content li {
	margin-bottom: 10px;
}
.active {
	background-color: #64b5f6;
}
